@import url('https://fonts.googleapis.com/css2?family=Josefin+Slab&family=Roboto:wght@400;500&display=swap');

*{
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
}

body{
  display: flex;
  justify-content: center;
  align-items: center;
  background: #1a1a1a;
}

.container{
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  margin: 0 auto;
  width: 16rem;
  text-align: center;
  border: 10px solid #000000;
  background: #000000;
  border-radius: 8px;
  box-shadow: 14px 17px 48px -6px rgba(0,0,0,0.75);
-webkit-box-shadow: 14px 17px 48px -6px rgba(0,0,0,0.75);
-moz-box-shadow: 14px 17px 48px -6px rgba(0,0,0,0.75);
 }

.keypad{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(4rem,auto);
  
}

input[type="text"]{
  height: 10rem;
  width: 16rem;
  background-color: #000000;
  border: 0px;
  color:#ffff;
  text-align: right;
  font-size: 30px;
  font-weight: 300;
  letter-spacing: 1px;
 
  
}




button{
  font-size: 22px;
  margin: 7px;
  border: none;
  border-radius: 100%;
  cursor: pointer;
  background: #1a1a1a;
  color: #ffff;
  
}

input:focus,
input:active,
button:focus,
button:active{
  outline: none;
}

button:active{
  background: #2d2d2d;
}

.highlight{
  background: #323232;
}

.result-highlight{
  background-color: #e86c30;
}